  methods: {
    GetRandomNum(Min, Max) {
      let Range = Max - Min;
      let Rand = Math.random();
      return Min + Math.round(Rand * Range);
    },
    drawLots(arr) {
      const length = arr.length;
      return this.GetRandomNum(0, length - 1);
    },
    drawFn() {
      this.drawContent = [];
      this.intermediate.map((item) => {
        this.drawContent.push(item.content[this.drawLots(item.content)]);
      });
    },
  },
